.boton-clear{
    height: 80px;
    font-size: 1.6em;
    display: felx;
    flex: 1;
    background-color: rgb(107, 5, 5);
    margin-top: 8px;
    justify-content: center;
    text-align: center;
    align-items: center;
    font-weight: bold;
    color: white;
    border: 2px solid white;
    cursor: pointer;
    border-radius: 25px;
}

.boton-clear:hover{
    background-color: rgb(104, 125, 22);
}